/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const planeState = createSlice({
  name: 'plane',
  initialState: {
    value: false,
  },
  reducers: {
    setPlaneFalse: (state) => {
      state.value = false;
    },
    setPlaneTrue: (state) => {
      state.value = true;
    },
    setPlaneUndefined: (state) => {
      state.value = undefined;
    },
    planeByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setPlaneTrue,
  setPlaneFalse,
  setPlaneUndefined,
  planeByState,
} = planeState.actions;

export default planeState.reducer;
