import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { setFalse } from '../reducer/stateChange';

import floorData from '../db/floor.json';
import asset from '../db/assets.json';

export default function SectionInfo() {
  const dispatch = useDispatch();
  const zoom = useSelector((state) => state.zoom.value);

  const [visible, setVisible] = useState('unvisible');

  useEffect(() => {
    if (zoom) {
      setTimeout(() => {
        setVisible('fade-in');
      }, 1500);
    } else {
      setVisible('fade-out');
    }
  }, [zoom]);
  return (
    <div className={`section-information ${visible}`}>
      <div
        className="back"
        onClick={() => {
          dispatch(setFalse());
        }}
        role="presentation"
      >
        <img src={process.env.PUBLIC_URL + asset.icons[0].pre} alt="pre" />
      </div>
      <div className="floor-container">
        { floorData.floor.map((data, i) => (
          <div className="floors" key={data.value}>
            <div className="floor-number">
              <p className="body-large">
                {data.number}
              </p>
            </div>
            <Link to="/home/description">
              <div
                className="floor-pin"
                role="presentation"
                onClick={() => {
                  localStorage.setItem('value', (floorData.floor.length - 1) - i);
                }}
              >
                <div className="pin-head" />
                <hr className="pin-line" />
                <div className="pin-icon">
                  <img src={process.env.PUBLIC_URL + data.icon} alt={data.number} />
                </div>
                <div className="pin-title body-medium">
                  <p>
                    {data.title}
                  </p>
                </div>
              </div>
            </Link>
          </div>
        ))}
      </div>
    </div>
  );
}
