/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const floorPlan = createSlice({
  name: 'floorPlan',
  initialState: {
    value: false,
  },
  reducers: {
    setFloorPlanFalse: (state) => {
      state.value = false;
    },
    setFloorPlanTrue: (state) => {
      state.value = true;
    },
    setFloorPlanUndefined: (state) => {
      state.value = undefined;
    },
    floorPlanByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setFloorPlanFalse,
  setFloorPlanTrue,
  setFloorPlanUndefined,
  floorPlanByState,
} = floorPlan.actions;

export default floorPlan.reducer;
