import React from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

import FloorElevator from '../components/FloorElevator';
import FloorController from '../components/FloorController';

import asset from '../db/assets.json';
import floorData from '../db/floor.json';

export default function FloorInfo({
  setFloorValue,
  setFloorHeight,
  setPopup,
  floorValue,
  floorHeight,
  popup,
}) {
  const userMode = useSelector((state) => state.userMode.value);
  const totalFloorValue = useSelector((state) => state.valueGroup.totalFloorValue);
  return (
    <div className={`floor-information ${userMode ? 'hide' : 'show'}`}>
      <div className="button-pre">
        <Link to="/home">
          <img src={process.env.PUBLIC_URL + asset.icons[0].pre} alt="pre" />
        </Link>
      </div>
      {
        floorData.floor.map((data, i) => (
          <div
            className={`
              floor-information-container
              ${i === (totalFloorValue - floorValue)
              ? 'left-slide'
              : 'left-slide-hidden'}
            `}
            key={data.number}
          >
            <div className="floor-number">
              <h1 className={`rolling-title title ${i === (totalFloorValue - floorValue) ? 'rolling' : ''}`}>
                {data.number}
              </h1>
            </div>
            <h2 className="floor-title heading-large">
              {data.title}
            </h2>
            <div className="floor-spec">
              {
                data.spec?.map((el) => (
                  <div
                    className="spec"
                  >
                    <p className="sub-heading">
                      {el.name}
                    </p>
                    <p className="body-large">
                      {el.content}
                    </p>
                  </div>
                ))
              }
            </div>
            <div className="floor-description">
              <p className="body-medium">
                {data.desc}
              </p>
            </div>
          </div>
        ))
      }
      <FloorElevator
        setFloorHeight={setFloorHeight}
        setFloorValue={setFloorValue}
        setPopup={setPopup}
        floorValue={floorValue}
        popup={popup}
      />
      <FloorController
        setFloorValue={setFloorValue}
        setFloorHeight={setFloorHeight}
        setPopup={setPopup}
        floorValue={floorValue}
        floorHeight={floorHeight}
        popup={popup}
      />
    </div>
  );
}
