import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setPlaneTrue, setPlaneFalse } from '../reducer/planeState';
import { setUserModeTrue, setUserModeFalse } from '../reducer/userModeState';
import { setFloorPlanTrue, setFloorPlanFalse } from '../reducer/floorPlanState';

import assets from '../db/assets.json';

export default function FloorNav() {
  const dispatch = useDispatch();

  const plane = useSelector((state) => state.plane.value);
  const userMode = useSelector((state) => state.userMode.value);
  const floorPlan = useSelector((state) => state.floorPlan.value);

  return (
    <div className="floor-navigation">
      {plane !== true
        ? (
          <div
            className="button button-user"
            role="presentation"
            onClick={() => {
              if (userMode === true) {
                dispatch(setUserModeFalse());
              } else {
                dispatch(setUserModeTrue());
              }
            }}
          >
            <i>
              {!userMode
                ? <img src={process.env.PUBLIC_URL + assets.icons[0].user} alt="user-journey-empty" />
                : <img src={process.env.PUBLIC_URL + assets.icons[0].userFill} alt="user-journey-filled" />}
            </i>
          </div>
        )
        : (
          <div
            className="button button-drawing"
            role="presentation"
            onClick={() => {
              if (floorPlan === true) {
                dispatch(setFloorPlanFalse());
              } else {
                dispatch(setFloorPlanTrue());
              }
            }}
          >
            <i>
              <img src={process.env.PUBLIC_URL + assets.icons[0].zone} alt="drawings" />
            </i>
          </div>
        )}
      <div
        className="button button-top-view"
        role="presentation"
        onClick={() => {
          if (plane === false) {
            dispatch(setPlaneTrue());
          } else if (plane === true) {
            dispatch(setPlaneFalse());
            dispatch(setFloorPlanFalse());
          }
          dispatch(setUserModeFalse());
        }}
      >
        <i>
          {!plane
            ? <img src={process.env.PUBLIC_URL + assets.icons[0].twoD} alt="top-view" />
            : <img src={process.env.PUBLIC_URL + assets.icons[0].threeD} alt="original-view" />}
        </i>
      </div>
    </div>
  );
}
