import React from 'react';
import { Html } from '@react-three/drei';
import { useSelector } from 'react-redux';

import floorData from '../db/floor.json';

export default function FloorUserMap({
  floorValue,
  i,
}) {
  const userMode = useSelector((state) => state.userMode.value);

  return (
    <Html
      className={`
        user-map
        ${userMode ? 'show-' : 'hide-'}user-map
        ${floorValue === floorData.floor[i].value ? 'show-' : 'hide-'}user-map`}
    >
      <img src={process.env.PUBLIC_URL + floorData.floor[floorValue].user[1]} alt={`plceholder ${floorValue}`} />
    </Html>
  );
}
