import React from 'react';
import { useSelector } from 'react-redux';

import asset from '../db/assets.json';

export default function FloorController({
  setFloorValue,
  setFloorHeight,
  floorValue,
  floorHeight,
}) {
  const moveValue = useSelector((state) => state.valueGroup.moveValue);
  const totalFloorValue = useSelector((state) => state.valueGroup.totalFloorValue);
  return (
    <div className="floor-change-controller">
      <div
        className="button button-up"
        role="presentation"
        onClick={() => {
          if (floorValue < totalFloorValue) {
            setFloorValue(floorValue + 1);
            setFloorHeight(floorHeight - moveValue);
          } else if (floorValue === totalFloorValue) {
            setFloorHeight(floorHeight);
          }
        }}
      >
        <img src={process.env.PUBLIC_URL + asset.icons[0].up} alt="up" />
      </div>
      <div
        className="button button-down"
        role="presentation"
        onClick={() => {
          if (floorValue > 0) {
            setFloorValue(floorValue - 1);
            setFloorHeight(floorHeight + moveValue);
          } else if (floorHeight === 0) {
            setFloorHeight(floorHeight);
          }
        }}
      >
        <img src={process.env.PUBLIC_URL + asset.icons[0].down} alt="down" />
      </div>
    </div>
  );
}
