/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const fadeState = createSlice({
  name: 'fade',
  initialState: {
    value: false,
  },
  reducers: {
    setFadeFalse: (state) => {
      state.value = false;
    },
    setFadeTrue: (state) => {
      state.value = true;
    },
    setFadeUndefined: (state) => {
      state.value = undefined;
    },
    fadeByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setFadeTrue,
  setFadeFalse,
  setFadeUndefined,
  fadeByState,
} = fadeState.actions;

export default fadeState.reducer;
