import React from 'react';
import { Html } from '@react-three/drei';
import { useSelector } from 'react-redux';

import floorData from '../db/floor.json';

export default function FloorPlan({
  floorValue,
  i,
}) {
  const plane = useSelector((state) => state.plane.value);
  const floorPlan = useSelector((state) => state.floorPlan.value);
  return (
    <Html
      className={`
        floor-plan
        ${floorPlan && plane ? 'show-' : 'hide-'}floor-plan
        ${floorValue === floorData.floor[i].value ? 'show-' : 'hide-'}floor-plan`}
    >
      <img src={process.env.PUBLIC_URL + floorData.floor[i].floorPlan} alt="floor plan" />
    </Html>
  );
}
