import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { setFadeFalse } from '../reducer/fadeState';

import facilityData from '../db/facility.json';
import asset from '../db/assets.json';

export default function FloorPopup({
  floorValue,
  setPopup,
  popup,
}) {
  const dispatch = useDispatch();

  const totalFloorValue = useSelector((state) => state.valueGroup.totalFloorValue);
  const currentPopup = facilityData.floor[Math.abs(floorValue - totalFloorValue)];
  const popupContent = currentPopup.facilities[popup].content;
  const [currentMenu, setCurrentMenu] = useState(0);

  const fade = useSelector((state) => state.fade.value);

  return (
    <div className={`floor-popup ${fade === true ? 'fade-in-right' : ''}`}>
      <div
        className="button-close"
        role="presentation"
        onClick={() => {
          dispatch(setFadeFalse());
          setTimeout(() => {
            setPopup(0);
            setCurrentMenu(0);
          }, 500);
        }}
      >
        <img src={process.env.PUBLIC_URL + asset.icons[0].close} alt="close" />
      </div>
      <p>{currentPopup.title}</p>
      <div className="popup-header">
        <div className="popup-title">
          {/* <i className="popup-icon">
            <img
              src={
                process.env.PUBLIC_URL
                + currentPopup.facilities[popup].icon
                }
              alt={currentPopup.facilities[popup].name}
            />
          </i> */ }
          <h2 className="heading-medium">
            {currentPopup.facilities[popup].name}
          </h2>
        </div>
        <div className="popup-content-navigation">
          <ul>
            {
              popupContent.map((data, i) => (
                <li
                  className="body-large"
                  role="presentation"
                  key={data.name}
                  onClick={() => {
                    setCurrentMenu(i);
                  }}
                >
                  {data.name}
                </li>
              ))
            }
          </ul>
        </div>
      </div>
      <div className="popup-text-content">
        {popupContent[currentMenu].data[0].name
          ? (
            <h4 className="sub-heading">
              {popupContent[currentMenu].data[0].name}
            </h4>
          )
          : null}
        {popupContent[currentMenu].data[0].desc
          ? (
            <p className="description body-medium">
              {popupContent[currentMenu].data[0].desc}
            </p>
          )
          : null}
      </div>
      <div className="popup-image-content">
        <img src={process.env.PUBLIC_URL + popupContent[currentMenu].data[0].images[0]} alt="a" />
      </div>
    </div>
  );
}
