/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const userModeState = createSlice({
  name: 'User',
  initialState: {
    value: false,
  },
  reducers: {
    setUserModeFalse: (state) => {
      state.value = false;
    },
    setUserModeTrue: (state) => {
      state.value = true;
    },
    setUserModeUndefined: (state) => {
      state.value = undefined;
    },
    userModeByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setUserModeTrue,
  setUserModeFalse,
  setUserModeUndefined,
  userModeByState,
} = userModeState.actions;

export default userModeState.reducer;
