import React from 'react';
import { useSelector } from 'react-redux';

import floorData from '../db/floor.json';

export default function FloorUserPath({
  floorValue,
}) {
  const userMode = useSelector((state) => state.userMode.value);
  return (
    <div className={`user-path-map ${userMode ? 'show-' : 'hide-'}user-path-map`}>
      <img src={process.env.PUBLIC_URL + floorData.floor[floorValue].user[0]} alt={`plceholder ${floorValue}`} />
    </div>
  );
}
