import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';

import floorData from '../db/floor.json';

export default function FloorElevator({
  setFloorHeight,
  setFloorValue,
  floorValue,
}) {
  const totalFloorValue = useSelector((state) => state.valueGroup.totalFloorValue);

  useEffect(() => {
    localStorage.setItem('value', (totalFloorValue - floorValue));
  });

  return (
    <div className="floor-elevator-container">
      {
        floorData.floor.map((data, i) => (
          <div
            className="floor-elevator"
            key={data.value}
          >
            <div
              className={`floor-icon ${i === (totalFloorValue - floorValue) ? 'highlight' : ''}`}
              role="presentation"
              onClick={() => {
                setFloorValue(totalFloorValue - i);
                setFloorHeight((totalFloorValue - i) * -2.5);
              }}
            />
            <p className="number sub-heading">
              {data.number}
            </p>
          </div>
        ))
      }
    </div>
  );
}
