import * as THREE from 'three';
import React from 'react';
import { useGLTF } from '@react-three/drei';
import { useFrame } from '@react-three/fiber';

import { useDispatch, useSelector } from 'react-redux';
import { setTrue, setUndefined } from '../reducer/stateChange';

export default function Building() {
  const zoom = useSelector((state) => state.zoom.value);
  const dispatch = useDispatch();

  const vec = new THREE.Vector3();

  useFrame((state) => {
    if (zoom === true) {
      state.camera.fov = THREE.MathUtils.lerp(state.camera.fov, 50, 0.04);
      state.camera.position.lerp(vec.set(22, 0, 0), 0.04);
      state.camera.lookAt(0, 0, 0);
      state.camera.updateProjectionMatrix();
    } else if (zoom === false) {
      state.camera.position.lerp(vec.set(22, 35, 35), 0.04);
      setTimeout(() => {
        dispatch((setUndefined()));
      }, 1400);
    }
    return null;
  });

  return (
    <group>
      <mesh
        onClick={() => {
          dispatch(setTrue());
        }}
      >
        <boxGeometry args={[4, 17.2, 4]} scale={0.2} />
        <meshStandardMaterial color="#333333" />
      </mesh>
    </group>
    /*
    <primitive
      object={model.scene}
      ref={buildingRef}
      position={[0, 0, 0]}
      onClick={() => {
        dispatch(setTrue());
      }}
    />
<group
      {...props}
      dispose={null}
      ref={buildingRef}
      position={[0, -6.5, 0]}
    >
      <mesh
        geometry={nodes.Cube002.geometry}
        material={materials['Material.002']}
        position={[0.58, 6.31, 0.46]}
        scale={[1.92, 8.5, 1.92]}
      >
        <meshStandardMaterial color="#3f3f3f" />
      </mesh>
      <Suspense fallback={null}>
        <primitive object={seesawBuilding} />
      </Suspense>
    </group>
    */
  );
}

useGLTF.preload('/building.glb');
