/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const stateChange = createSlice({
  name: 'zoom',
  initialState: {
    value: undefined,
  },
  reducers: {
    setTrue: (state) => {
      state.value = true;
    },
    setFalse: (state) => {
      state.value = false;
    },
    setUndefined: (state) => {
      state.value = undefined;
    },
    changeByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setTrue,
  setFalse,
  setUndefined,
  changeByState,
} = stateChange.actions;

export default stateChange.reducer;
