/* eslint-disable react/no-unknown-property */
import React, { useState, useEffect } from 'react';
import { Canvas } from '@react-three/fiber';
import { useSelector } from 'react-redux';

import FloorNav from '../templates/FloorNav';
import FloorInfo from '../templates/FloorInfo';
// import FloorFacility from '../templates/FloorFacility';
// <FloorFacility floorValue={floorValue} />

import FloorPopup from '../components/FloorPopup';
import FloorUserPath from '../components/FloorUserPath';

import FloorModel from '../models/FloorModel';

// import floorData from '../db/floor.json';

export default function Floor() {
  const moveValue = useSelector((state) => state.valueGroup.moveValue);

  const [floorValue, setFloorValue] = useState(JSON.parse(localStorage.getItem('value')));
  const [floorHeight, setFloorHeight] = useState(floorValue * -moveValue);
  const [popup, setPopup] = useState(0);

  useEffect(() => {
    localStorage.setItem('value', floorValue);
    localStorage.setItem('floor Height', floorHeight);
  }, [floorValue, floorHeight]);

  return (
    <div className="floor">
      <div className="floor-function-container">
        <FloorInfo
          setFloorValue={setFloorValue}
          setFloorHeight={setFloorHeight}
          setPopup={setPopup}
          floorValue={floorValue}
          floorHeight={floorHeight}
          popup={popup}
        />
        <FloorNav
          setFloorValue={setFloorValue}
          setFloorHeight={setFloorHeight}
          floorValue={floorValue}
          floorHeight={floorHeight}
        />
      </div>
      <FloorPopup
        floorValue={floorValue}
        setPopup={setPopup}
        popup={popup}
      />
      <FloorUserPath
        floorValue={floorValue}
      />
      <Canvas
        camera={{
          fov: 2.5,
          position: [-70, 30, 80],
        }}
      >
        <FloorModel
          setPopup={setPopup}
          floorValue={floorValue}
          floorHeight={floorHeight}
          popup={popup}
        />
        <pointLight position={[-10, 20, 20]} />
      </Canvas>
    </div>
  );
}
