/* eslint-disable no-nested-ternary */
/* eslint-disable max-len */
/* eslint-disable no-param-reassign */
/* eslint-disable react/no-unknown-property */
/* eslint-disable react/jsx-props-no-spreading */
import * as THREE from 'three';
import React, { useRef } from 'react';
import { useFrame } from '@react-three/fiber';
import { useSelector } from 'react-redux';
import facilityData from '../db/facility.json';

import FloorPins from '../components/FloorPins';
import FloorUserMap from '../components/FloorUserMap';
import FloorPlan from '../components/FloorPlan';

import LobbyModel from './LobbyModel';

function FloorModel({
  setPopup,
  floorValue,
  floorHeight,
}) {
  const groupRef = useRef();
  const meshRef = useRef([]);
  const materialRef = useRef([]);
  const vec = new THREE.Vector3();

  const plane = useSelector((state) => state.plane.value);
  const userMode = useSelector((state) => state.userMode.value);
  const moveValue = useSelector((state) => state.valueGroup.moveValue);

  useFrame((state) => {
    if (plane) {
      groupRef.current.position.lerp(vec.set(0.7, floorHeight, 0), 0.1);
    } else if (userMode) {
      groupRef.current.position.lerp(vec.set(0, floorHeight, 0), 0.1);
    } else {
      groupRef.current.position.lerp(vec.set(0, floorHeight, -0.4), 0.1);
    }
    state.camera.updateProjectionMatrix();
    return null;
  });

  useFrame((state) => {
    if (plane) {
      state.camera.position.lerp(vec.set(0, 160, 1), 0.07);
      state.camera.fov = THREE.MathUtils.lerp(state.camera.fov, 2, 0.2);
      state.camera.lookAt(0, 0, 0);
    }
    return null;
  });

  useFrame((state) => {
    if (plane === false) {
      state.camera.position.lerp(vec.set(-70, 30, 80), 0.2);
      state.camera.fov = THREE.MathUtils.lerp(state.camera.fov, 2, 0.2);
      state.camera.lookAt(0, 0, 0);
    }
    return null;
  });

  return (
    <group
      position={[0, 0, 0]}
      ref={groupRef}
    >
      {
        facilityData.floor.map((data, i) => (
          <mesh
            className={`meshs ${data.value}`}
            position={[0, (data.value * moveValue), 0]}
            key={data.value}
            ref={(el) => { meshRef.current.push(el); }}
          >
            <LobbyModel
              i={i}
              floorValue={floorValue}
              ref={materialRef}
            />
            <FloorPins
              i={i}
              data={data}
              floorValue={floorValue}
              setPopup={setPopup}
              ref={materialRef}
            />
            <FloorUserMap
              i={i}
              floorValue={floorValue}
            />
            <FloorPlan
              i={i}
              floorValue={floorValue}
            />
          </mesh>
        ))
      }
    </group>
  );
}

export default FloorModel;
